#st-nav a {
  font-family: Roboto, sans-serif;
  font-weight: lighter;
}

#st-nav ul {
  position: fixed;
  width: 90%;
  max-width: 400px;
  left: unset;
  right: 5%;
  bottom: 20px;
  border-radius: 0.25em;
  background: rgba(12, 22, 30, 0.9);
  visibility: hidden;
  /* remove overflow:hidden if you want to create a drop-down menu
     - but then remember to fix/eliminate the list items animation */
  overflow: hidden;
  z-index: 9990;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
  -webkit-transition: -webkit-transform 0.3s, visibility 0s 0.3s;
  -webkit-transition: visibility 0s 0.3s, -webkit-transform 0.3s;
  transition: visibility 0s 0.3s, -webkit-transform 0.3s;
  -o-transition: transform 0.3s, visibility 0s 0.3s;
  transition: transform 0.3s, visibility 0s 0.3s;
  transition: transform 0.3s, visibility 0s 0.3s, -webkit-transform 0.3s;
}
#st-nav ul li {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
#st-nav ul.is-visible {
  visibility: visible;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  -webkit-transition: -webkit-transform 0.3s, visibility 0s 0s;
  -webkit-transition: visibility 0s 0s, -webkit-transform 0.3s;
  transition: visibility 0s 0s, -webkit-transform 0.3s;
  -o-transition: transform 0.3s, visibility 0s 0s;
  transition: transform 0.3s, visibility 0s 0s;
  transition: transform 0.3s, visibility 0s 0s, -webkit-transform 0.3s;
}
#st-nav ul.is-visible li:nth-child(1) {
  /* list items animation */
  -webkit-animation: st-slide-in 0.2s;
  animation: st-slide-in 0.2s;
}
#st-nav ul.is-visible li:nth-child(2) {
  -webkit-animation: st-slide-in 0.3s;
  animation: st-slide-in 0.3s;
}
#st-nav ul.is-visible li:nth-child(3) {
  -webkit-animation: st-slide-in 0.4s;
  animation: st-slide-in 0.4s;
}
#st-nav ul.is-visible li:nth-child(4) {
  -webkit-animation: st-slide-in 0.5s;
  animation: st-slide-in 0.5s;
}
#st-nav ul.is-visible li:nth-child(5) {
  -webkit-animation: st-slide-in 0.6s;
  animation: st-slide-in 0.6s;
}

#st-nav li a {
  display: inline-block;
  padding: 1.6em;
  width: 100%;
  cursor: pointer;
}

#st-nav li:last-child a {
  border-bottom: none;
}

#st-nav #st-main-nav ul li a {
  color: #ffffff;
  text-decoration: none;
}

#st-nav .st-nav-trigger {
  position: fixed;
  bottom: 20px;
  right: 5%;
  width: 44px;
  height: 44px;
  background: white;
  border-radius: 0.25em;
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  z-index: 9999;
}
#st-nav .st-nav-trigger span {
  /* the span element is used to create the menu icon */
  position: absolute;
  display: block;
  width: 20px;
  height: 2px;
  top: 50%;
  margin-top: -1px;
  left: 50%;
  margin-left: -10px;
  -webkit-transition: background 0.3s;
  -o-transition: background 0.3s;
  transition: background 0.3s;
}
#st-nav .st-nav-trigger span:before,
#st-nav .st-nav-trigger span:after {
  content: '';
  position: absolute;
  left: 0;
  background: inherit;
  width: 100%;
  height: 100%;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: -webkit-transform 0.3s, background 0s;
  -webkit-transition: background 0s, -webkit-transform 0.3s;
  transition: background 0s, -webkit-transform 0.3s;
  -o-transition: transform 0.3s, background 0s;
  transition: transform 0.3s, background 0s;
  transition: transform 0.3s, background 0s, -webkit-transform 0.3s;
}
#st-nav .st-nav-trigger span:before {
  top: -6px;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
}
#st-nav .st-nav-trigger span:after {
  bottom: -6px;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
}
#st-nav .st-nav-trigger.menu-is-open {
  -webkit-box-shadow: none;
          box-shadow: none;
}
#st-nav .st-nav-trigger.menu-is-open span {
  background: rgba(232, 74, 100, 0);
}
#st-nav .st-nav-trigger.menu-is-open span:before {
  background: #7100b7;
  top: 0;
  -webkit-transform: rotate(135deg);
  -ms-transform: rotate(135deg);
  transform: rotate(135deg);
}
#st-nav .st-nav-trigger.menu-is-open span:after {
  background: #7100b7;
  bottom: 0;
  -webkit-transform: rotate(225deg);
  -ms-transform: rotate(225deg);
  transform: rotate(225deg);
}

@-webkit-keyframes st-slide-in {
  0% {
    -webkit-transform: translateX(100px);
  }
  100% {
    -webkit-transform: translateY(0);
  }
}

@keyframes st-slide-in {
  0% {
    -webkit-transform: translateX(100px);
    -ms-transform: translateX(100px);
    transform: translateX(100px);
  }
  100% {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
}

@-webkit-keyframes st-bounce-in {
  0% {
    -webkit-transform: scale(0);
  }
  60% {
    -webkit-transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
  }
}

@keyframes st-bounce-in {
  0% {
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
  }
  60% {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }
}

@media only screen and (max-width: 991px) {
  #st-nav ul {
    margin-bottom: 0;
    padding-left: 0;
  }
}

@media only screen and (min-width: 992px) {
  #st-nav .navUIContainer {
    position: absolute;
    width: 100%;
  }
  #st-nav ul {
    /* the navigation moves to the top */
    position: relative;
    width: 100%;
    text-align: center;
    max-width: none;
    left: 0;
    right: 0;
    bottom: auto;
    top: 60px;
    background: transparent;
    visibility: visible;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-transition: all 0s;
    -o-transition: all 0s;
    transition: all 0s;
  }
  #st-nav li {
    display: inline-block;
  }
  #st-nav li a {
    opacity: 1;
    padding: 20px 20px;
    border-bottom: none;
  }
  #st-nav.is-fixed ul {
    position: fixed;
    width: 90%;
    max-width: 400px;
    left: unset;
    right: 5%;
    bottom: 20px;
    top: auto;
    background: rgba(12, 22, 30, 0.9);
    visibility: hidden;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
  }
  #st-nav.is-fixed ul li {
    display: block;
  }
  #st-nav.is-fixed ul li a {
    padding: 1em;
    margin-left: 0;
    color: #ffffff;
    text-align: left;
  }
  #st-nav ul.has-transitions {
    -webkit-transition: -webkit-transform 0.3s, visibility 0s 0.3s;
    -webkit-transition: visibility 0s 0.3s, -webkit-transform 0.3s;
    transition: visibility 0s 0.3s, -webkit-transform 0.3s;
    -o-transition: transform 0.3s, visibility 0s 0.3s;
    transition: transform 0.3s, visibility 0s 0.3s;
    transition: transform 0.3s, visibility 0s 0.3s, -webkit-transform 0.3s;
  }
  #st-nav ul.is-visible {
    visibility: visible;
    margin-bottom: 0;
    padding-left: 0;
    z-index: 9998;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-transition: -webkit-transform 0.3s, visibility 0s 0s;
    -webkit-transition: visibility 0s 0s, -webkit-transform 0.3s;
    transition: visibility 0s 0s, -webkit-transform 0.3s;
    -o-transition: transform 0.3s, visibility 0s 0s;
    transition: transform 0.3s, visibility 0s 0s;
    transition: transform 0.3s, visibility 0s 0s, -webkit-transform 0.3s;
  }
  #st-nav ul.is-hidden {
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    -o-transition: transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
  }
  #st-nav .st-nav-trigger {
    visibility: hidden;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
    -webkit-transition: -webkit-transform 0.3s, visibility 0s 0.3s;
    -webkit-transition: visibility 0s 0.3s, -webkit-transform 0.3s;
    transition: visibility 0s 0.3s, -webkit-transform 0.3s;
    -o-transition: transform 0.3s, visibility 0s 0.3s;
    transition: transform 0.3s, visibility 0s 0.3s;
    transition: transform 0.3s, visibility 0s 0.3s, -webkit-transform 0.3s;
  }
  #st-nav.is-fixed .st-nav-trigger {
    visibility: visible;
    -webkit-transition: all 0s;
    -o-transition: all 0s;
    transition: all 0s;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-animation: st-bounce-in 0.3s linear;
    animation: st-bounce-in 0.3s linear;
  }
}

@media only screen and (max-width: 767px) and (orientation: landscape) {
  #st-nav li a {
    padding: 1.4em;
  }
}

@media only screen and (max-width: 600px) and (orientation: landscape) {
  #st-nav li a {
    padding: 1em;
  }
}

/* Underline From Center */
.hvr-underline-from-center {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0);
          box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  overflow: hidden;
}
.hvr-underline-from-center:before {
  content: '';
  position: absolute;
  z-index: -1;
  left: 50%;
  right: 50%;
  bottom: 0;
  background: rgba(255, 255, 255, 0.7);
  height: 4px;
  -webkit-transition-property: left, right;
  -o-transition-property: left, right;
  transition-property: left, right;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
     transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  -o-transition-timing-function: ease-out;
     transition-timing-function: ease-out;
}
.hvr-underline-from-center:hover:before,
.hvr-underline-from-center:focus:before,
.hvr-underline-from-center:active:before {
  left: 0;
  right: 0;
}

.st-nav-trigger {
  -webkit-box-shadow: 0 0 10px #7100b7;
          box-shadow: 0 0 10px #7100b7;
}

.st-nav-trigger span {
  background: #7100b7;
}

#st-nav #st-main-nav ul.is-visible {
  -webkit-box-shadow: 0 0 10px #7100b7;
          box-shadow: 0 0 10px #7100b7;
}



