.team-section {
  background: url('./images/team_bg.png') no-repeat;
  background-size: 100% 100%;
}
.team-section .container .title {
  font-size: 4.3em;
  font-family: Montserrat-ExtraBold, sans-serif;
}
@media only screen and (min-width: 600px) {
  .team-section .container .title {
    font-size: 4.3em;
  }
}
@media only screen and (max-width: 599px) {
  .team-section .container .title {
    font-size: 60px;
  }
}
@media only screen and (max-width: 500px) {
  .team-section .container .title {
    font-size: 55px;
  }
}
.team-section .container .teamDescription {
  font-size: 20px;
  width: 70%;
  margin: 0 auto;
  font-family: 'Yantramanav';
}
@media only screen and (max-width: 992px) {
  .team-section .container .teamDescription {
    font-size: 17px;
  }
}
@media only screen and (max-width: 767px) {
  .team-section .container .teamDescription {
    font-size: 16px;
  }
}
@media only screen and (max-width: 576px) {
  .team-section .container .teamDescription {
    font-size: 15px;
    width: 90%;
  }
}
.team-section .logo {
  position: absolute;
  right: 0;
  width: 50%;
  -webkit-transform: translateY(-250px);
      -ms-transform: translateY(-250px);
          transform: translateY(-250px);
}
@media (min-width: 1400px) {
  .team-section .members .carousel-div {
    -ms-flex: 0 0 130%;
        flex: 0 0 130%;
    max-width: 150%;
    -webkit-transform: translateX(-301px);
        -ms-transform: translateX(-301px);
            transform: translateX(-301px);
  }
}
@media only screen and (max-width: 1400px) {
  .team-section .members .carousel-div {
    -ms-flex: 0 0 100%;
        flex: 0 0 100%;
    max-width: 100%;
    -webkit-transform: translateX(0px);
        -ms-transform: translateX(0px);
            transform: translateX(0px);
  }
}
.team-section .members .carousel {
  z-index: 100;
}
.team-section .members .carousel .carousel-control .arrowLeft {
  width: 80px;
  height: 80px;
  background: -webkit-gradient(linear, right top, left top, from(#243eff), color-stop(#196cff), to(#00d2ff));
  background: -webkit-linear-gradient(right, #243eff, #196cff, #00d2ff);
  background: -o-linear-gradient(right, #243eff, #196cff, #00d2ff);
  background: linear-gradient(to left, #243eff, #196cff, #00d2ff);
  border-radius: 40px;
  padding-top: 22px;
  position: absolute;
  left: -57px;
  top: 230px;
  opacity: 0.8;
  z-index: 100;
}
.team-section .members .carousel .carousel-control .arrowRight {
  width: 80px;
  height: 80px;
  background: -webkit-gradient(linear, left top, right top, from(#243eff), color-stop(#196cff), to(#00d2ff));
  background: -webkit-linear-gradient(left, #243eff, #196cff, #00d2ff);
  background: -o-linear-gradient(left, #243eff, #196cff, #00d2ff);
  background: linear-gradient(to right, #243eff, #196cff, #00d2ff);
  border-radius: 40px;
  padding-top: 22px;
  position: absolute;
  right: -65px;
  top: 230px;
  opacity: 0.8;
  z-index: 100;
}
@media only screen and (max-width: 767px) {
  .team-section .members .carousel .carousel-control .arrowLeft {
    left: -47px;
  }
  .team-section .members .carousel .carousel-control .arrowRight {
    right: -54px;
  }
}

@media only screen and (max-width: 800px) {
  .team-section {
    background: url('./images/team_bg_small.png') no-repeat;
    background-size: 100% 100%;
  }
}



