.stay-section .container .title {
  font-size: 40px;
  font-family: Roboto, sans-serif;
  font-weight: bold;
  text-transform: uppercase;
}

.stay-section .container .description {
  width: 60%;
  margin: 0 auto;
  font-family: 'Roboto Regular', sans-serif;
}

@media only screen and (max-width: 767px) {
  .stay-section .container .description {
    width: 90%;
    margin: 0 auto;
  }
}

.stay-section .container .stayCenter {
  width: 70%;
}

.stay-section .leftCloud {
  z-index: -1;
  width: 30%;
  margin-right: 70%;
  display: block;
  min-height: 1px;
}

.stay-section .cloud-parallax-img {
  margin-top: -500px;
  overflow: visible;
  width: 100% !important;
  height: auto !important;
  right: 0;
}

.stay-section .cloud-parallax {
  overflow: visible !important;
  z-index: -100;
}



