.whitepaper-section {
  background: white;
}
.whitepaper-section .top_bg {
  width: 100%;
  margin-bottom: 200px;
  height: 100%;
}
.whitepaper-section .container .title {
  font-size: 4.3em;
  color: #01013c;
  font-family: Montserrat-ExtraBold, sans-serif;
}
@media only screen and (min-width: 600px) {
  .whitepaper-section .container .title {
    font-size: 70px;
  }
}
@media only screen and (max-width: 599px) {
  .whitepaper-section .container .title {
    font-size: 60px;
  }
}
@media only screen and (max-width: 500px) {
  .whitepaper-section .container .title {
    font-size: 50px;
  }
}
@media only screen and (max-width: 430px) {
  .whitepaper-section .container .title {
    font-size: 45px;
  }
}
@media only screen and (max-width: 375px) {
  .whitepaper-section .container .title {
    font-size: 40px;
  }
}
@media only screen and (max-width: 320px) {
  .whitepaper-section .container .title {
    font-size: 35px;
  }
}
.whitepaper-section .container .description {
  width: 65%;
  color: #52a6e6;
  margin: 0 auto;
  font-family: 'Roboto Regular', sans-serif;
}
@media only screen and (max-width: 992px) {
  .whitepaper-section .container .description {
    width: 95%;
  }
}
.whitepaper-section .container .wp_top_icons {
  margin-right: -10px;
  margin-left: -10px;
  opacity: 0.8;
  width: 100px;
  height: 100px;
}
@media only screen and (max-width: 450px) {
  .whitepaper-section .container .wp_top_icons {
    width: 65px;
    height: 65px;
  }
}
.whitepaper-section .container .downloadButton {
  border-style: none;
  background: -webkit-gradient(linear, left top, right top, from(#243eff), color-stop(#196cff), to(#00d2ff));
  background: -webkit-linear-gradient(left, #243eff, #196cff, #00d2ff);
  background: -o-linear-gradient(left, #243eff, #196cff, #00d2ff);
  background: linear-gradient(to right, #243eff, #196cff, #00d2ff);
  color: white;
  font-size: 23px;
  font-family: 'Myriad Pro Regular', sans-serif;
  padding: 10px 20px;
  -webkit-box-shadow: 2px 2px 10px 10px rgba(6, 161, 255, 0.2);
          box-shadow: 2px 2px 10px 10px rgba(6, 161, 255, 0.2);
}
.whitepaper-section .container .downloadButton:hover {
  background: -webkit-gradient(linear, left top, right top, from(#196cff), color-stop(#196cff), to(#196cff));
  background: -webkit-linear-gradient(left, #196cff, #196cff, #196cff);
  background: -o-linear-gradient(left, #196cff, #196cff, #196cff);
  background: linear-gradient(to right, #196cff, #196cff, #196cff);
}
@media only screen and (max-width: 360px) {
  .whitepaper-section .container .downloadButton {
    font-size: 20px;
  }
}
.whitepaper-section .centerImage1 {
  -webkit-transform: translateX(70px) translateY(20px);
      -ms-transform: translateX(70px) translateY(20px);
          transform: translateX(70px) translateY(20px);
}
.whitepaper-section .leftEffect {
  position: absolute;
  left: 0;
  height: 50%;
  width: 20%;
}
.whitepaper-section .bottom_bg {
  margin-top: 100px;
  width: 100%;
  height: 100%;
}



