.profile {
  width: 100%;
}
.profile .photo {
  border-radius: 50%;
  z-index: 1;
  -webkit-filter: grayscale(100%);
  /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}
.profile .name {
  font-size: 26px;
  text-transform: uppercase;
  font-family: Montserrat-ExtraBold, sans-serif;
}
.profile .role {
  font-size: 19px;
  color: #00d2ff;
  text-transform: uppercase;
  height: 40px;
  font-family: 'Yantramanav';
  font-weight: bold;
}
.profile .description {
  width: 85%;
  height: 120px;
  margin: 0 auto;
  font-family: 'Yantramanav';
  color: black;
  font-size: 17px;
  text-align: justify;
  text-align-last: center;
}
.profile .linkedin {
  position: absolute;
  bottom: 30px;
  left: 50%;
}



